	import common from '../../common/common.js'
	import util from '../../common/util.js'
	
	export default {
		data() {
			return {
				baseUrl:common.baseUrl,
				userid:'',
				userInfo:[],
				orderno:'',
				status:'',
				pay_status:0,
				ps_status:0,
				pay_type:1,			//支付方式
				orderList:[],
				all_amount:0,		//订单总金额
				all_realamount:0,	//订单实际金额
				all_cha:0,			//订单差额
				all_cha_abs:0,		//差额绝对值
				bzfee:0,			//包装费
				psfee:0,			//配送费
				disamount:0,		//优惠
				orderinfo:[],		//包含配送信息的订单信息,单条
				addtime:'',
				isneworder:0,		//是否新订单，120秒内
				counttime:'',		//倒计时
				psuser:[],			//配送人员
				maishou:[],			//买手信息
				coupon_disamount:0,	//优惠券减免
				orderQR:'',			//订单二维码
				showOrderQR:false,	//是否显示核销码
				
				info:{
					delta:2,
				},
				titleHeight:0,
				statusBarHeight:0,
				naviBarHeight:0,
			};
		},
		onLoad(e) {
			var that = this
			var userid = uni.getStorageSync('userid');
			var userInfo = uni.getStorageSync('userInfo');
			console.log(userInfo);
			that.userid = userid;
			that.userInfo = userInfo;
			var orderno = e.id;
			that.orderno = orderno
			that.getOrderInfo(orderno);
			
			that.titleHeight = common.titleHeight;
			that.statusBarHeight = common.statusBarHeight;
			that.naviBarHeight = common.naviBarHeight;
			
			var act = e.act;
			if(act == 'payfee'){
				that.payfee();
			}
			
			
		},
		onPullDownRefresh() {
			var that = this
			uni.showNavigationBarLoading();
			console.log('下拉刷新中...');	
			var orderno = that.orderno;

			that.getOrderInfo();
			
			uni.hideNavigationBarLoading();
			uni.stopPullDownRefresh();
		},
		methods:{
			toggleOrderQR(){
				var that = this
				var showOrderQR = that.showOrderQR
				that.showOrderQR = !showOrderQR
			},
			//核销码
			orderQRCode(){
				var that = this
				var orderno = that.orderno
				uni.request({
					url: common.baseUrl + 'sharepic/getOrderQR',
					method: 'GET',
					data: {
						orderno:orderno
					},
					success: res => {
						console.log("orderQR",res);
						that.orderQR = res.data.orderQR
						that.toggleOrderQR();
					},
					fail: () => {},
					complete: () => {}
				});
				
			},
			//收货
			shouhuo(){
				var that = this
				
				var cha = parseFloat(that.all_cha);
				
				uni.showModal({
					title:'提示',
					content:'确认收货吗？',
					success(res) {
						if(res.confirm){
							if(cha > 0){
								//补差价
								that.buCha();
							}else if(cha < 0){
								//退差价								
								that.tuiCha();
							}else{
								//无差价
								console.log(cha);
								that.shouhuoSubmit();
							}
						}
						
					}
				})
				
			},
			//补差价
			buCha(){
				var that = this
				uni.request({
					url: common.apiUrl + 'bucha',
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' 
					},
					data: {
						userid:that.userid,
						orderno:that.orderno,
						cha:that.all_cha_abs
					},
					success: res => {
						var data = res.data
						if(data.code == 0){
							//余额补差完成
							uni.showToast({
								title:'余额补差成功',
								duration:1000,
								icon:'success',
								success(ress) {
								  setTimeout(function () {
									that.getOrderInfo();
								  }, 1500) //延迟时间
								}
							})
						}else if(data.code == 1){
							//余额不够，现金来凑
							var payfee = data.payfee
							var order_no = data.order_no
							that.buchaPayfee(order_no,payfee);
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//补差支付订单
			buchaPayfee(orderno,payfee){
				var that = this
				var userid = that.userid
				uni.request({
					url: common.apiUrl+'buchaPayfee',
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' 
					},
					data: {
						userid:userid,
						order_no:orderno,
						payfee:payfee
					},
					success: res => {
						console.log(res)
						var timeStamp = res.data.timeStamp
						var nonceStr = res.data.nonceStr;
						var vpackage = res.data.package;
						var paySign = res.data.paySign;
						//console.log("nonceStr",nonceStr);
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: timeStamp,
							nonceStr: nonceStr,
							package: vpackage,
							signType: 'MD5',
							paySign: paySign,
							success: function (res) {								
								uni.showToast({
									title: '支付成功',
									icon: 'success',
									duration: 2000,
									success(ress) {
									  setTimeout(function () {
										uni.reLaunch({
										  url: '/pages/orders/orders'
										})
									  }, 1000) //延迟时间
									}
								  });
							},
							fail: function (err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
					},
					fail: () => {},
					complete: () => {}
				});
				//console.log(that.userInfo);
				//console.log('开始支付');
			},
			
			//退差价
			tuiCha(){
				var that = this
				uni.request({
					url: common.apiUrl + 'tuiCha',
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' 
					},
					data: {
						userid:that.userid,
						orderno:that.orderno,
						cha:that.all_cha_abs
					},
					success: res => {
						var data = res.data
						console.log(data)
						if(data.code == 0){
							uni.showToast({
								title: '操作成功,退款已入账户钱包',
								duration: 1000,
								icon :'none'
							})
							that.getOrderInfo();
						}else{
							uni.showToast({
								title: data.msg,
								duration: 1000,
								icon :'none'
							})
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			//确认收货提示
			confirmShouhuo(){
				var that = this
				uni.showModal({
					title:'提示',
					content:'确认收货吗？',
					success() {
						that.shouhuoSubmit();
					}
				})
			},
			//确认收货操作
			shouhuoSubmit(){
				var that = this
				uni.request({
					url: common.apiUrl + 'shouhuo',
					method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' 
					},
					data: {
						userid:that.userid,
						orderno:that.orderno
					},
					success: res => {
						var data = res.data
						console.log(data);
						if(data.code == 0){
							uni.showToast({
								title:'操作成功',
								duration:1000,
								icon:'success'
							})
							that.getOrderInfo();
						}
					},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//取消订单
			cancel(){
				var that = this
				var userid = that.userid
				var orderno = that.orderno
				uni.showModal({
					title:'提示',
					content:'确定要取消订单吗？',
					success(res) {
						if (res.confirm) {
							uni.request({
								url: common.apiUrl + 'cancelOrder',
								method: 'POST',
					header: {
						'content-type': 'application/x-www-form-urlencoded' 
					},
								data: {
									userid:userid,
									orderno:orderno
								},
								success: res => {
									console.log(res)
									if(res.data.code == ''){
										uni.showToast({
											title:'操作成功',
											icon:'success',
											duration: 1000,
											success(ress) {
											  setTimeout(function () {
												that.getOrderInfo();
											  }, 1000) //延迟时间
											}
										})
										
									}
								},
								fail: () => {},
								complete: () => {}
							});
						}
						
					}
				})
				
			},
			//授权并支付
			authpay(){
				var that = this
				uni.login({
				  provider: 'weixin',
				  success: function (loginRes) {
					console.log(loginRes);
					var code = loginRes.code;
					// 获取用户信息
					uni.getUserInfo({
					  provider: 'weixin',
					  success: function (infoRes) {
						
						var rawData = infoRes.rawData;
						var signature = infoRes.signature;
						var iv =  infoRes.iv;
						var encryptedData = infoRes.encryptedData;
						uni.request({
							url: common.apiUrl+'login/updateUserInfo',
							method: 'GET',
							data: {
								userid:that.userid,
								code:code,
								rawData:rawData,
								signature:signature,
								iv:iv,
							},
							success: res => {
								console.log(res);
								if(res.data.code == 0){
									var userInfo  = res.data.userInfo;
									uni.setStorageSync('userInfo',userInfo);
									that.userInfo = userInfo;
									that.payfee();
								}else{
									uni.showToast({
										title: res.data.msg,
										duration: 1000,
										icon :'none'
									})
								}
							},
							fail: () => {},
							complete: () => {}
						});
						
					  }
					});
				  }
				});
			},
			
			//支付订单
			payfee(){
				var that = this
				var userid = that.userid
				var orderno = that.orderno
				console.log(userid);
				console.log('orderno',orderno)
// 				if(that.all_amount <= that.coupon_disamount){
// 					return false
// 				}
				uni.request({
					url: common.apiUrl+'pay/payfee',
					method: 'GET',
					data: {
						userid:userid,
						order_no:orderno
					},
					success: res => {
						console.log(res)
						var timeStamp = res.data.timeStamp
						var nonceStr = res.data.nonceStr;
						var vpackage = res.data.package;
						var paySign = res.data.paySign;
						//console.log("nonceStr",nonceStr);
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: timeStamp,
							nonceStr: nonceStr,
							package: vpackage,
							signType: 'MD5',
							paySign: paySign,
							success: function (res) {
								uni.showModal({
									title: '提示',
									content: '支付成功,您的订单将在'+that.orderinfo.pstime +'　'+ that.orderinfo.order_pstime_para+'左右送达',
									showCancel: false,
									cancelText: '',
									confirmText: '确定',
									success: res => {
										setTimeout(function () {
											uni.reLaunch({
											  url: '/pages/orders/orders'
											})
										}, 1000) //延迟时间
									},
									fail: () => {},
									complete: () => {}
								});
								// uni.showToast({
								// 	title: '支付成功',
								// 	icon: 'success',
								// 	duration: 2000,
								// 	success(ress) {
								// 	  setTimeout(function () {
								// 		uni.reLaunch({
								// 		  url: '/pages/orders/orders'
								// 		})
								// 	  }, 1000) //延迟时间
								// 	}
								//   });
							},
							fail: function (err) {
								console.log('fail:' + JSON.stringify(err));
							}
						});
					},
					fail: () => {},
					complete: () => {}
				});
				//console.log(that.userInfo);
				//console.log('开始支付');
			},
			
			//获取订单列表
			getOrderInfo(){
				var that = this
				var orderno = that.orderno;
				console.log(orderno);
				uni.request({
					url: common.apiUrl+'orders/getOrderInfo',
					method: 'GET',
					data: {
						orderno:orderno
					},
					success: res => {
						console.log(res);
						that.orderList = res.data.order_list
						that.status = res.data.orderinfo.status;
						that.ps_status = res.data.orderinfo.order_ps_status;
						that.pay_status = res.data.orderinfo.order_status;
						that.pay_type = res.data.orderinfo.order_paytype
						that.coupon_disamount = res.data.coupon_disamount;
						that.all_amount = res.data.all_amount;
						that.all_realamount = res.data.all_realamount;
						that.all_cha = res.data.all_cha;
						that.all_cha_abs = res.data.all_cha_abs;
						that.disamount = res.data.disamount;
						that.psfee = res.data.psfee;
						that.bzfee = res.data.bzfee;
						that.orderinfo = res.data.orderinfo;
						that.addtime = res.data.orderinfo.order_addtime
						that.psuser = res.data.psuser
						that.maishou = res.data.maishou
						
						//that.cancelOrderCountDown();
					},
					fail: () => {},
					complete: () => {}
				});
			},
			
			//取消订单倒计时
			cancelOrderCountDown(){
				var that = this
				var addtime = that.addtime
				var mdate = new Date();
				var now = mdate.getTime();
				now = now/1000;
				console.log(now);
				if(now-addtime < 60 && that.pay_status >= 0){
					var ltime = 60-(now-addtime);
					var ss = util.formatTime(ltime);
					that.counttime = ss
					
					//setInterval(cancelOrderCountDown,1000);
					setTimeout(function() {  
						that.cancelOrderCountDown()  
					},  
					1000)
					that.isneworder = 1;
				}else{
					clearTimeout(function(){
						that.cancelOrderCountDown()
					})
					that.isneworder = 0;
				}
				
			},
			
			//联系骑手
			contactRider(){
				var that = this
				var psuser = that.psuser
				if(psuser != ''){
					uni.makePhoneCall({
						phoneNumber:psuser.mem_mobile
					})
				}else{
					uni.showToast({
						title:'获取骑手信息失败',
						duration:1000
					})
				}
			},
			
			//联系买手
			contactMaishou(){
				var that = this
				var maishou = that.maishou
				if(maishou != ''){
					uni.makePhoneCall({
						phoneNumber:maishou.mem_mobile
					})
				}else{
					uni.showToast({
						title:'获取买手信息失败',
						duration:1000
					})
				}
			}
			
			
			

		}
	}
